
import { Options, Vue } from "vue-class-component";
import { Plotter, PlotterToolSelect } from "@/lib/Plotter";

@Options({
  props: {
    plotter: Plotter,
  },
})
export default class LoggerDisplay extends Vue {
  plotter!: Plotter;

  x = 0.0;
  y = 0.0;
  rate = 0.0;

  async setNone(): Promise<void> {
    await this.plotter.sendToolSelect(PlotterToolSelect.NONE);
  }
  async setPen(): Promise<void> {
    await this.plotter.sendToolSelect(PlotterToolSelect.PEN);
  }
  async setEraser(): Promise<void> {
    await this.plotter.sendToolSelect(PlotterToolSelect.ERASER);
  }
}
